body {
    margin: 0;
    font-family: "Alegreya Sans", sans-serif;
    display: flex;
    flex-direction: column;
    min-height: 100vh; 
  }
  
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(37, 25, 25);
    padding: 10px;
    color: #FFFFFF;
    margin-bottom: 0;
  }
  
  .logo {
    img {
      height: 50px;
      width: 50px;
      transition: transform 0.3s ease;
    }
  }
  
  a:hover .logo {
    transform: scale(1.1);
  }
  
  .title {
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    font-family: 'Familjen Grotesk', sans-serif;
  }
  
  .main-title {
    color: rgb(123, 26, 26);
    margin: 0;
    font-size: 2em;
  }
  
  .sub-title {
    color: beige;
    margin: 0;
    font-size: 1.2em;
  }
  
  .menu {
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-wrap: wrap;
  
      li {
        margin-left: 20px;
  
        a {
          text-decoration: none;
          color: white;
          font-weight: bold;
  
          &:hover {
            color: rgb(123, 26, 26);
          }
        }
      }
    }
  }
  
  main {
    flex: 1; /* Permite que el main ocupe el espacio restante */
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px; /* Espacio entre el header y la imagen */
    
    .imagen-container {
      position: relative;
      width: 100%;
      padding-top: 31px;
  
      .imagen {
        width: 100vw;
        height: auto;
        opacity: 0.7;
        border-radius: 0;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
      }
  
      .texto-overlay {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: rgb(123, 26, 26);
        font-size: 5em;
        font-weight: 700;
        text-align: center;
        white-space: nowrap;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
      }
    }
  
    .blanc-text {
      color: beige;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    }
  }
  
  footer {
    text-align: center;
    padding: 10px;
    background-color: rgb(37, 25, 25);
    color: white;
  }
  
  /*Media Queries para Responsividad*/
  @media (max-width: 768px) {
    header {
      flex-direction: column;
      align-items: center;
      padding: 10px;
    }
  
    .menu {
      ul {
        flex-direction: column;
        align-items: flex-end;
      }
  
      li {
        margin: 5px 0;
      }
    }
  
    .imagen-container {
      .texto-overlay {
        font-size: 1.5em;
      }
    }
  }
  
  @media (max-width: 480px) {
    .imagen-container {
      .texto-overlay {
        font-size: 1.2em;
      }
    }
  }